@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
}

.login-container {
  display: flex;
  justify-content: center;
  height: 100vh;
  background-image: url(/KBSC-REACT/static/media/login-background-img.8ed59a0a.jpg);
  background-size: 100vw 100vh;
}

.login-button-container {
  margin-top: 68vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
}

.login-back {
  position: fixed;
  top: 3vh;
  left: 3vw;
  border: none;
  width: 2em;
  height: 2em;
  cursor: pointer;
  border-radius: 50%;
  transition: 0.2s;
}

.login-back:hover {
  padding: 0.8em;
  transform: translateX(-0.7em) translateY(-0.7em);
  background: rgba(100, 100, 100, 0.263);
  transition: 0.2s;
}

.login-button-container > p {
  font-size: 0.7rem;
}

.login-button-container .login-icon {
  width: 25%;
  border: 0px;
  height: 50px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 27px;
  padding-right: 27px;
  border-radius: 8px;
  cursor: pointer;
  background: #fdfdfd;
  border: 0.6px solid #f0f0f0b3;
  box-shadow: rgba(230, 230, 230, 0.863) 1px 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 12px;
  gap: 12px;
  transition: 0.4s;
}

.login-button-container .login-icon:focus {
  background: rgba(220, 220, 220, 1);
  box-shadow: rgba(100, 100, 100, 0.863) 1px 1px;
  border: 0.6px solid #999;
  transition: 0.4s;
}

.login-google-icon {
  height: 1em;
  width: 1em;
  color: #444;
}

.login-icon span {
  font-size: 0.9rem;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}

@font-face {
  font-family: "RixYeoljeongdo_Regular";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2102-01@1.0/RixYeoljeongdo_Regular.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: less-light;
  src: url(/KBSC-REACT/static/media/Less.753f01ef.otf) format("opentype");
  font-weight: normal;
  font-style: normal;
}

body {
  padding: 0;
  margin: 0;
  height: 100vh;
}

.home {
  height: 100vh;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(/KBSC-REACT/static/media/main-background-img.b73ad35f.jpg);
  background-size: 100vw 100vh;
}

.home-Logo {
  padding: 0.6vh;
  position: fixed;
  left: 3vw;
  top: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.home-Logo p:nth-child(1) {
  color: black;
  font-family: "RixYeoljeongdo_Regular";
  font-size: 5vh;
  margin: 0;
  letter-spacing: 0.3vh;
}

.home-Logo p:nth-child(2) {
  font-family: less-light;
  margin: 0;
  color: black;
  font-size: 3vh;
  letter-spacing: 0.47vh;
}

.home > nav {
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(60vh + 6px + 2vh);
}

.home-nav {
  list-style: none;
  width: calc(60vh + 4px + 2vh);
  display: grid;
  margin: 0;
  padding: 0;
  grid-gap: 1vh;
  gap: 1vh;
  grid-template-columns: 1fr 1fr 1fr;
}

.home-nav li:nth-child(1) {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 3;
  width: calc(40vh + 1vh + 2px) !important;
  height: calc(
    40vh + 1vh + 2px
  ) !important; /* 순서대로 각 li의 높이의 2배 + gap + 테두리두꼐) */
  margin: 0;
}

.home-first .CardFront {
  width: calc(40vh + 1vh + 2px) !important;
  height: calc(40vh + 1vh + 2px) !important;
}

.CardInfo {
  top: 0;
  left: 0;
  right: 0;
  font-size: 0.8rem;
  transform: translateY(calc(41vh + 4px - 100%))
    translateX(calc(41vh - 100% + 4px));
  width: 25% !important;
  border-radius: 100% 0% 0% 100% / 100% 30% 70% 0%;
  height: 25% !important;
  text-align: right;
  white-space: pre;
  color: white;
  background: #2e303d;
  position: absolute;
  opacity: 0.95;
  transition: 0.12s ease-in-out;
}

.CardFront:hover .CardInfo {
  opacity: 0;
  transition: 0.12s ease-in-out;
}

.home-first .CardBack {
  width: calc(40vh + 1vh + 2px) !important;
  height: calc(40vh + 1vh + 2px) !important;
}

.home-nav li {
  text-align: center;
  height: 20vh;
  width: 20vh;
  border-radius: 10px 10px 0% 10px / 10px 10px 0% 10px;
  box-shadow: rgb(0 0 0 / 2%) -1px 1px 1px;
  background: rgb(255, 255, 255);
  border: 1px solid rgba(70, 77, 82, 0.082);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.home-nav li svg {
  height: 6vh;
  width: 6vh;
}

.home-nav li p {
  margin-top: 1.7vh !important;
  margin-bottom: 0 !important;
  font-size: 2.3vh;
}

.home-nav li a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-decoration: none;
  color: black;
}

.home-nav:nth-child(1) div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

