  border: 1px yellow solid !important;
  background-color: khaki !important;
}

.mission-middle-box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 5vw;
  gap: 5vw;
  width: 100%;
}

.mission-Container button {
  width: 12vh;
  height: 6vh;
}

.mission-Container ul li {
  list-style: none;
  height: 8vh;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.mission-Container ul li p {
  margin: 0;
}

.mission-Container ul li a {
  text-decoration: none;
  margin: 0;
}

.mission-Container ul li * {
  margin-top: 0.7em !important;
  margin-bottom: 0.7em !important;
}

.mission-eval li {
  justify-content: center !important;
  letter-spacing: 0.6px;
}

.mission-no {
  color: lightslategrey;
}

